export default function MobileNavigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState("hot");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <BottomNavigation
      className={classes.root}
      value={value}
      onChange={handleChange}
    >
      <BottomNavigationAction label="Hot" value="hot" icon={<FavoriteIcon />} />
      <BottomNavigationAction
        label="Search"
        value="search"
        icon={<SearchIcon />}
      />
      <BottomNavigationAction
        label="Location"
        value="location"
        icon={<LocationIcon />}
      />
      <BottomNavigationAction
        label="Cart"
        value="cart"
        icon={
          <Badge badgeContent={6} color="secondary">
            <ShoppingIcon />
          </Badge>
        }
      />
    </BottomNavigation>
  );
}
